import {
  Container,
  CssBaseline,
  ThemeProvider,
  createTheme,
} from "@mui/material";
import { useAppContext } from "./AppContext";
import FileUpload from "./FileUpload";
import Toolbar from "./Toolbar";

const theme = createTheme({
  palette: {
    mode: "light",
  },
});

const AppContent: React.FC = () => {
  const { fileData } = useAppContext();
  const hasLoadedFile = Boolean(fileData?.nodesById && fileData?.references);

  return (
    <Container maxWidth={false}>
      <FileUpload />
      {hasLoadedFile && <Toolbar />}
    </Container>
  );
};

const App: React.FC = () => {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <AppContent />
    </ThemeProvider>
  );
};

export default App;
