import { Box, Paper, Typography } from "@mui/material";
import React from "react";
import { useAppContext } from "./AppContext";
import { Node } from "./types";

interface ReferencesListProps {
  node: Node;
}

const ReferencesList: React.FC<ReferencesListProps> = ({ node }) => {
  const { fileData, helpers } = useAppContext();

  const getReferencedNodes = () => {
    if (!fileData?.references) return [];
    return fileData.references
      .filter((ref) => ref.sourceId === node.id)
      .map((ref) => helpers.getNodeById(ref.targetId))
      .filter((node): node is Node => node !== undefined);
  };

  const getReferencingNodes = () => {
    if (!fileData?.references) return [];
    return fileData.references
      .filter((ref) => ref.targetId === node.id)
      .map((ref) => helpers.getNodeById(ref.sourceId))
      .filter((node): node is Node => node !== undefined);
  };

  const referencedNodes = getReferencedNodes();
  const referencingNodes = getReferencingNodes();

  return (
    <Paper sx={{ p: 2 }}>
      <Box sx={{ mb: 2 }}>
        <Typography variant="h6">References</Typography>
        {referencedNodes.length > 0 ? (
          referencedNodes.map((refNode) => (
            <Box key={refNode.id} sx={{ mb: 1 }}>
              <Typography variant="body1">{refNode.name}</Typography>
              <Typography variant="caption" color="text.secondary">
                {refNode.type}
              </Typography>
            </Box>
          ))
        ) : (
          <Typography variant="body2" color="text.secondary">
            No references found
          </Typography>
        )}
      </Box>
      <Box>
        <Typography variant="h6">Referenced By</Typography>
        {referencingNodes.length > 0 ? (
          referencingNodes.map((refNode) => (
            <Box key={refNode.id} sx={{ mb: 1 }}>
              <Typography variant="body1">{refNode.name}</Typography>
              <Typography variant="caption" color="text.secondary">
                {refNode.type}
              </Typography>
            </Box>
          ))
        ) : (
          <Typography variant="body2" color="text.secondary">
            No referencing nodes found
          </Typography>
        )}
      </Box>
    </Paper>
  );
};

export default ReferencesList;
