import { Search } from "@mui/icons-material";
import {
  Box,
  Checkbox,
  FormControl,
  FormControlLabel,
  FormGroup,
  InputAdornment,
  Paper,
  TextField,
  Typography,
} from "@mui/material";
import React, { useState } from "react";
import { useAppContext } from "./AppContext";
import { FilterOptions, Node, NodeType } from "./types";

interface FieldsListProps {
  onFieldSelect: (field: Node | null) => void;
}

const FieldsList: React.FC<FieldsListProps> = ({ onFieldSelect }) => {
  const { helpers } = useAppContext();
  const [filterOptions, setFilterOptions] = useState<FilterOptions>({
    searchTerm: "",
    selectedTypes: ["column", "calculation"],
  });

  const nodes = helpers.getNodes();

  const filterNodes = (nodes: Node[], options: FilterOptions) => {
    return nodes.filter((node) => {
      const matchesSearch =
        node.name.toLowerCase().includes(options.searchTerm.toLowerCase()) ||
        node.caption
          ?.toLowerCase()
          .includes(options.searchTerm.toLowerCase()) ||
        false;
      const matchesType = options.selectedTypes.includes(node.type);
      return matchesSearch && matchesType;
    });
  };

  const handleSearchChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setFilterOptions((prev) => ({
      ...prev,
      searchTerm: event.target.value,
    }));
  };

  const handleTypeChange = (type: NodeType) => {
    setFilterOptions((prev) => {
      const selectedTypes = prev.selectedTypes.includes(type)
        ? prev.selectedTypes.filter((t) => t !== type)
        : [...prev.selectedTypes, type];
      return {
        ...prev,
        selectedTypes,
      };
    });
  };

  const filteredNodes = filterNodes(nodes, filterOptions);

  return (
    <Paper sx={{ p: 2 }}>
      <Box sx={{ mb: 2 }}>
        <TextField
          fullWidth
          placeholder="Search fields..."
          value={filterOptions.searchTerm}
          onChange={handleSearchChange}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <Search />
              </InputAdornment>
            ),
          }}
        />
      </Box>
      <FormControl component="fieldset" sx={{ mb: 2 }}>
        <FormGroup row>
          <FormControlLabel
            control={
              <Checkbox
                checked={filterOptions.selectedTypes.includes("column")}
                onChange={() => handleTypeChange("column")}
              />
            }
            label="Columns"
          />
          <FormControlLabel
            control={
              <Checkbox
                checked={filterOptions.selectedTypes.includes("calculation")}
                onChange={() => handleTypeChange("calculation")}
              />
            }
            label="Calculations"
          />
        </FormGroup>
      </FormControl>
      <Box>
        {filteredNodes.map((node) => (
          <Box
            key={node.id}
            onClick={() => onFieldSelect(node)}
            sx={{
              p: 1,
              cursor: "pointer",
              "&:hover": {
                backgroundColor: "action.hover",
              },
            }}
          >
            <Typography variant="subtitle1">{node.name}</Typography>
            <Typography variant="caption" color="text.secondary">
              {node.type}
            </Typography>
          </Box>
        ))}
      </Box>
    </Paper>
  );
};

export default FieldsList;
