import { Clear, Upload } from "@mui/icons-material";
import { Button, CircularProgress, Stack, Typography } from "@mui/material";
import React, { useRef, useState } from "react";
import { useAppContext } from "./AppContext";
import {
  CalculationNode,
  ColumnNode,
  FileData,
  Node,
  Reference,
} from "./types";

const FileUpload: React.FC = () => {
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [isLoading, setIsLoading] = useState(false);
  const { fileData, setFileData } = useAppContext();

  const hasLoadedFile = Boolean(fileData?.nodesById && fileData?.references);

  const onFileChange = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    setIsLoading(true);
    try {
      const text = await file.text();
      const parser = new DOMParser();
      const xmlDoc = parser.parseFromString(text, "text/xml");

      const nodes: Node[] = [];
      const references: Reference[] = [];

      // Parse columns
      const columns = xmlDoc.querySelectorAll("column");
      columns.forEach((column: Element) => {
        const node: ColumnNode = {
          id: column.getAttribute("name") || "",
          name:
            column.getAttribute("caption") || column.getAttribute("name") || "",
          type: "column",
          caption: column.getAttribute("caption") || undefined,
          dataType: column.getAttribute("datatype") || undefined,
          role: column.getAttribute("role") || undefined,
          description: column.getAttribute("description") || undefined,
          formula: column.getAttribute("formula") || undefined,
          aggregation: column.getAttribute("aggregation") || undefined,
          defaultFormat: column.getAttribute("default-format") || undefined,
        };
        nodes.push(node);
      });

      // Parse calculations
      const calculations = xmlDoc.querySelectorAll("calculation");
      calculations.forEach((calc: Element) => {
        const node: CalculationNode = {
          id: calc.getAttribute("name") || calc.getAttribute("caption") || "",
          name: calc.getAttribute("caption") || calc.getAttribute("name") || "",
          type: "calculation",
          caption: calc.getAttribute("caption") || undefined,
          formula: calc.getAttribute("formula") || undefined,
          dataType: calc.getAttribute("datatype") || undefined,
          description: calc.getAttribute("description") || undefined,
          calculation: calc.textContent || undefined,
        };
        nodes.push(node);
      });

      // Build references
      calculations.forEach((calc: Element) => {
        const sourceId =
          calc.getAttribute("name") || calc.getAttribute("caption") || "";
        const calculation = calc.textContent || "";

        nodes.forEach((targetNode) => {
          if (calculation.includes(`[${targetNode.name}]`)) {
            references.push({
              sourceId,
              targetId: targetNode.id,
              type: "direct",
            });
          }
        });
      });

      const nodesById = new Map(nodes.map((node) => [node.id, node]));

      const newFileData: FileData = {
        filename: file.name,
        nodesById,
        references,
      };

      setFileData(newFileData);
      console.log(
        `Processed ${nodes.length} nodes and ${references.length} references`
      );
      console.log("Sample node:", nodes[0]);
      console.log("Sample reference:", references[0]);
    } catch (error) {
      console.error("Error processing file:", error);
    } finally {
      setIsLoading(false);
      if (fileInputRef.current) {
        fileInputRef.current.value = "";
      }
    }
  };

  const onClearFile = () => {
    setFileData(null);
    if (fileInputRef.current) {
      fileInputRef.current.value = "";
    }
  };

  return (
    <Stack direction="row" spacing={2} alignItems="center" sx={{ my: 2 }}>
      <input
        type="file"
        accept=".twb"
        onChange={onFileChange}
        style={{ display: "none" }}
        ref={fileInputRef}
      />
      <Button
        variant="contained"
        onClick={() => fileInputRef.current?.click()}
        startIcon={<Upload />}
        disabled={isLoading}
      >
        Upload Tableau Workbook
      </Button>
      {isLoading && <CircularProgress size={24} />}
      {hasLoadedFile && !isLoading && fileData && (
        <>
          <Typography>{fileData.filename}</Typography>
          <Button
            variant="outlined"
            onClick={onClearFile}
            startIcon={<Clear />}
          >
            Clear
          </Button>
        </>
      )}
    </Stack>
  );
};

export default FileUpload;
